<template>
  <div class="home">
    <div class="home-top">
      <div v-for="(item, i) in navArr" :key="i" class="home-item">
        <img class="icon" :src="item.icon" />
        <div>
          <div class="top-title">{{ item.title }}</div>
          <div>{{ item.num }}</div>
        </div>
      </div>
    </div>
    <Echarts v-if="flag" :options="options" />
  </div>
</template>

<script>
import Echarts from "@/components/Echarts";
import { api_getHomeData } from "@/api/order";
export default {
  components: {
    Echarts,
  },

  data() {
    return {
      flag:false,
      navArr: [
        { icon: require("@/assets/img/icon1.png"), title: "总订单", num: "" },
        { icon: require("@/assets/img/icon2.png"), title: "总销售额", num: "" },
        { icon: require("@/assets/img/icon3.png"), title: "今日订单", num: "" },
        {
          icon: require("@/assets/img/icon4.png"),
          title: "今日总销售额",
          num: "",
        },
      ],
      options: {
        title: "首页数据", // 标题
        legend: ["首页数据"], // 图表图例
        xData: [], // X轴的数据
        orderData: [], // 订单数据
        amountData: [], // 价格数据
      },
    };
  },
  methods: {
    async getHomeData() {
      let {
        todayOrder,
        totalAmount,
        totalOrder,
        totayAmount,
        amountData,
        orderData,
        xData
      } = await api_getHomeData();
      //导航赋值
      let numArr = [totalOrder, totalAmount, todayOrder, totayAmount];
      this.navArr.forEach((item, i) => (item.num = numArr[i]));
      //定义标杆，防止数据不能渲染
      this.flag = true;
      //赋值
      this.options.xData = xData
      this.options.orderData = orderData
      this.options.amountData = amountData
    },
  },
  created() {
    this.getHomeData();
  },
};
</script>

<style lang="less" scoped>
.home {
  .home-top {
    width: 100%;
    height: 80px;
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .home-item {
      width: 24%;
      height: 80px;
      background: #fff;
      border-radius: 7px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      font-size: 18px;
      .icon {
        height: 55px;
        width: 55px;
      }
      .top-title {
        font-size: 16px;
        color: #646464;
        margin-bottom: 8px;
      }
    }
  }
  #box {
    width: 100%;
    height: 400px;
    background: #fff;
  }
}
</style>